// Hide Show Password
$(".togglePassword").click(function () {
    $(this).toggleClass("ryd-eye ryd-eye-slash");
    var toggleInput = $(this).parent().find('.input')
    if (toggleInput.attr("type") == "password") {
        toggleInput.attr("type", "text");
    } else {
        toggleInput.attr("type", "password");
    }
});